*
{
    margin: 0;
    padding: 0;
    
}
.Admission
{
    max-width: 1000px;
    margin: 0 auto;
    margin-top: 90px;
    height: 100%;
    
   
}
.admission_title
{
    color:white;
    font-size: 30px;
    text-align: center;
}
.admission_container
{
    height: 65vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.admission_formcontainer
{
    box-shadow: 1px 4px 12px -2px rgba(0,0,0,4.3);
    background-color:rgb(5, 82, 248);
    padding: 30px;
    border-radius: 10px;
    width: 500px;
}
.admission_formcontainer input
{
    border: 1px solid white;
    outline: none;
    padding: 15px 10px;
    width: 80%;
    border-radius: 8px;
    margin: 10px 0;
    /* background-color: transparent; */
    color:black;
    font-size: 14px;
}
.admission_formcontainer button
{
    background-color: rgb(5, 247, 5);
    border: 1px solid white;
    outline: none;
    font-size: 18px;
    color: green;
    padding: 10px;
    width: 200px;
    margin-top: 20px;
    border-radius: 12px;
    cursor: pointer;
    margin-left: 100px;

}