*
{
    margin: 0;
    padding: 0;
}
.navActive
{
  color: rgb(6, 248, 6);
}
nav
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background-color:lightgray;
    color: black;
}
