nav img
{
    height: 100px;
    width: 400px;
}
nav ul
{
    display: flex;
    justify-content: space-between;
    width: 60%;
    align-items: center;
}
nav ul a
{
    font-size: 20px;
    font-weight: bold;
    color: black;
    text-decoration: none;
    transition: all 0.5s ease-in-out;
}
nav ul a:hover
{
  transform: scale(1.1);
  color: rgb(6, 250, 6);
}
