*
{
    margin: 0;
    padding: 0;
}
.user-container
{
    background-image: url(https://www.imageshine.in/uploads/gallery/blue-watercolor-decorative-Wallpaper-design.jpg);
    height: 100vh;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
}
.user-container h1
{
    color: white;
    text-align: center;
}
.user
{
    background-color: transparent;
    margin-bottom: 30px;
    width: 600px;
    padding: 20px;
    color: white;
    border: 2px solid rgb(241, 55, 164);
    box-shadow: -2px 9px 46px 3px rgba(235,12,153,0.75);
    margin-left: 100px;
    margin-top: 70px;
}
.user h2, .user h3
{
    color:coral;

}
.user span
{
    font-weight: bold;
    color: palegreen;
}
.user_delete
{
    border: none;
    outline: none;
    padding: 10px;
    width: 70px;
    background-color: red;
    border-radius: 8px;
    color: white;
    cursor: pointer;
    margin-left: 400px;
    font-weight: bold;
    font-size: 20px;
}
.user_bottom
{
    margin-bottom: 20px;
}